body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minuteshand, .stopwatch__secondshand {
  transform-origin: 50% 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%)rotate(0);
}

.stopwatch__minuteshand {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 600s steps(60, end) infinite step-minutes;
}

.stopwatch__secondshand {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 10s linear infinite spin-seconds;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes spin-seconds {
  from {
    transform: translate(-50%, -100%)rotate(0);
  }

  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

@keyframes step-minutes {
  from {
    transform: translate(-50%, -100%)rotate(0);
  }

  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}
